export default {
	data() {
		return {
			login: true,
			userText: '',
			pswdText: ''
		}
	},
	onLoad () {
		this.login = uni.getStorageSync('login');
	},
	methods: {
		handleRegistered () {
			uni.navigateTo({
				url: '../registered/registered'
			})
		},
		inputChange (e, val) {
			if (val == '账号') {
				this.userText = e.detail.value;
			} else if (val == '密码') {
				this.pswdText = e.detail.value;
			}
		},
		handleTo () {
			if (!this.login) {
				if (!this.userText || !this.pswdText) {
					uni.showToast({
						icon: 'none',
						title: '请填写账号或密码'
					});
					return false;
				} else if (this.userText != uni.getStorageSync('user')) {
					uni.showToast({
						icon: 'none',
						title: '账号错误'
					});
					return false;
				} else if (this.pswdText != uni.getStorageSync('pswd')) {
					uni.showToast({
						icon: 'none',
						title: '密码错误'
					});
					return false;
				} else {
					uni.showToast({
						icon: 'none',
						title: '登录成功'
					});
					setTimeout( () => {
						uni.switchTab({
							url: '../tabar/latest/latest'
						})
					}, 200)
				}
			} else {
				uni.setStorageSync('user', '');
				uni.setStorageSync('pswd', '');
				uni.showToast({
					icon: 'none',
					title: '注销成功'
				});
				this.login = false;
			}
		}
	}
}
